"use client";

import React, { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Skeleton } from "@/components/ui/skeleton";
import {
    User,
    Stethoscope,
    MapPin,
    Phone,
    Mail,
    Calendar,
    Award,
    CheckCircle,
    XCircle,
} from "lucide-react";
import { useSelector } from "react-redux";
import apiClient from "@/app/api-client";


const DoctorProfile = () => {
    const { _id } = useSelector((state: any) => state.user);
    const [profile, setProfile] = useState([]);
    const [loading, setLoading] = useState(true);


    const fetchDoctorProfile = async () => {
        try {
            setLoading(true);
            const res = await apiClient.get(`/doctorkycs/${_id}`);
            if (res?.data?.isKycSubmitted) {
                setProfile(res.data);
            } else {
                const response = await apiClient.get(`/user/${_id}`);
                setProfile(response.data);
            }
        } catch (error) {
            console.error("Error fetching doctor profile:", error);
        } finally {
            setLoading(false);
        }
    };
    useEffect(() => {
        fetchDoctorProfile();
    }, []);

    const isDoctorKyc = (data: FormData): data is DoctorKycData => {
        return "fullname" in data;
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-green-100 p-6">
                <div className="max-w-4xl mx-auto space-y-6">
                    <Skeleton className="h-8 w-64" />
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <Skeleton className="h-64" />
                        <Skeleton className="h-64" />
                    </div>
                    <Skeleton className="h-32" />
                </div>
            </div>
        );
    }

    if (!profile) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-green-100 flex items-center justify-center">
                <Card>
                    <CardContent className="p-6">
                        <p className="text-center text-gray-500">No profile data found</p>
                    </CardContent>
                </Card>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gradient-to-br from-green-50 via-white to-green-100 p-6">
            <div className="max-w-4xl mx-auto">
                <div className="mb-8">
                    <h1 className="text-3xl font-bold text-green-600 mb-2">Doctor Profile</h1>
                    <p className="text-gray-600">
                        {isDoctorKyc(profile)
                            ? "Complete profile information"
                            : "Basic profile information - KYC pending"}
                    </p>
                </div>

                {isDoctorKyc(profile) ? (
                    <div className="space-y-6">
                        {/* Header Card */}
                        <Card className="border-green-100">
                            <CardHeader className="bg-green-50">
                                <div className="flex items-center justify-between">
                                    <div className="flex items-center space-x-4">
                                        <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center">
                                            <Stethoscope className="w-8 h-8 text-green-600" />
                                        </div>
                                        <div>
                                            <CardTitle className="text-2xl text-green-600">
                                                {profile.fullname}
                                            </CardTitle>
                                            <p className="text-gray-600">{profile.degree}</p>
                                        </div>
                                    </div>
                                    <div className="flex flex-col items-end space-y-2">
                                        <Badge
                                            variant={profile.isKycApproved ? "default" : "secondary"}
                                            className={`${profile.isKycApproved
                                                ? "bg-green-500 text-white"
                                                : "border-green-500 text-green-600"
                                                }`}
                                        >
                                            {profile.isKycApproved ? (
                                                <>
                                                    <CheckCircle className="w-4 h-4 mr-1" />
                                                    KYC Approved
                                                </>
                                            ) : (
                                                <>
                                                    <XCircle className="w-4 h-4 mr-1" />
                                                    KYC Pending
                                                </>
                                            )}
                                        </Badge>
                                    </div>
                                </div>
                            </CardHeader>
                        </Card>

                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                            {/* Professional Details */}
                            <Card className="border-green-100">
                                <CardHeader className="bg-green-50">
                                    <CardTitle className="flex items-center text-green-600">
                                        <Award className="w-5 h-5 mr-2" />
                                        Professional Details
                                    </CardTitle>
                                </CardHeader>
                                <CardContent className="space-y-4">
                                    <div>
                                        <label className="text-sm font-medium text-green-700">NMC ID</label>
                                        <p className="text-lg">{profile.NMCID}</p>
                                    </div>
                                    <div>
                                        <label className="text-sm font-medium text-green-700">Experience</label>
                                        <p className="text-lg">{profile.experienceYear} years</p>
                                    </div>
                                    <div>
                                        <label className="text-sm font-medium text-green-700">Specializations</label>
                                        <div className="flex flex-wrap gap-2 mt-2">
                                            {profile.specializations.map((spec, index) => (
                                                <Badge
                                                    key={index}
                                                    variant="outline"
                                                    className="border-green-500 text-green-600"
                                                >
                                                    {spec}
                                                </Badge>
                                            ))}
                                        </div>
                                    </div>
                                </CardContent>
                            </Card>

                            {/* Experience History */}
                            <Card className="border-green-100">
                                <CardHeader className="bg-green-50">
                                    <CardTitle className="flex items-center text-green-600">
                                        <Calendar className="w-5 h-5 mr-2" />
                                        Experience History
                                    </CardTitle>
                                </CardHeader>
                                <CardContent>
                                    <div className="space-y-4">
                                        {profile.experience.map((exp, index) => (
                                            <div
                                                key={index}
                                                className="border-l-2 border-green-200 pl-4"
                                            >
                                                <p className="font-medium">{exp.body}</p>
                                                <p className="text-sm text-gray-600">
                                                    {new Date(exp.date).toLocaleDateString()}
                                                </p>
                                            </div>
                                        ))}
                                    </div>
                                </CardContent>
                            </Card>
                        </div>
                    </div>
                ) : (
                    // Basic User Profile
                    <div className="space-y-6">
                        <Card className="border-green-100">
                            <CardHeader className="bg-green-50">
                                <CardTitle className="flex items-center text-green-600">
                                    <User className="w-6 h-6 mr-2" />
                                    Basic Profile - KYC Required
                                </CardTitle>
                            </CardHeader>
                            <CardContent className="pt-6">
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                                    <div className="space-y-4">
                                        <div>
                                            <label className="text-sm font-medium text-green-700 flex items-center">
                                                <Mail className="w-4 h-4 mr-1" />
                                                Email
                                            </label>
                                            <p className="text-lg">{profile.email}</p>
                                        </div>
                                        <div>
                                            <label className="text-sm font-medium text-green-700 flex items-center">
                                                <Phone className="w-4 h-4 mr-1" />
                                                Phone Number
                                            </label>
                                            <p className="text-lg">{profile.phoneNumber}</p>
                                        </div>
                                    </div>
                                    <div className="space-y-4">
                                        <div>
                                            <label className="text-sm font-medium text-green-700 flex items-center">
                                                <User className="w-4 h-4 mr-1" />
                                                Role
                                            </label>
                                            <Badge
                                                variant="outline"
                                                className="capitalize border-green-500 text-green-600"
                                            >
                                                {profile.role}
                                            </Badge>
                                        </div>
                                        <div>
                                            <label className="text-sm font-medium text-green-700 flex items-center">
                                                <MapPin className="w-4 h-4 mr-1" />
                                                Location
                                            </label>
                                            <p className="text-lg">{profile.location}</p>
                                        </div>
                                    </div>
                                </div>
                                <div className="mt-6 p-4 bg-green-50 rounded-lg border border-green-100">
                                    <p className="text-green-700 font-medium">Complete KYC Required</p>
                                    <p className="text-green-600 text-sm mt-1">
                                        Please complete your KYC verification to access full profile features.
                                    </p>
                                </div>
                            </CardContent>
                        </Card>
                    </div>
                )}
            </div>
        </div>
    );
};

export default DoctorProfile;
